* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', sans-serif;
  background-color:  rgb(220, 196, 99);
  color:white;
}

/* HEADER */

nav, nav ul{
  font-size: 1.1em;
  list-style-type: none;
  margin: 0;
  padding: 0 .5em;
  overflow: hidden;
  background-color: rgb(43, 43, 209);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

nav ul li {
  float: right;
  padding: .6em;
}

nav ul li img {
  width: 20px;
  height: 20px;
  display: inline;
}

.my-name {
  padding-left: .5em;
  font-size: 2.5em;
}

/* LINKS */

a{
  text-decoration: none;
  color: white;
  font-size: 1.2em;
}

nav a:hover, 
nav a:focus {
  color: white;
  font-weight: bold;
}

/* MAIN */

.name h1 {
  font-size: 2em;
  font-weight: bold;
  text-align: start;
  margin-top: .5em;
}
.name h3 {
  font-size: 1.5em;
}

.name h2 {
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 1.4em;
}

main {
  display: flex;
  width: 100%;
  background-color: rgb(43, 43, 209);
  color: white;
}

.name {
  margin: 0;
  padding: 0 2em ;
  width: 70%;
}

.name-img img {
  margin: 4em;
  display: inline;
  width: 100%;
  max-width: 500px;
  height: auto;
  float: right;
  border-radius: 10px;
}

.download {
  padding: 1em;
  background-color: rgb(220, 196, 99);
  border-radius: 5px;
  color: rgb(0, 0, 0);
  font-size: 1.3em;
}

.download:hover, 
.download:focus {
  color: rgb(0, 0, 0);
  font-weight: bold;
  padding: 1.2em;
}

/* ABOUT ME */

.about-title {
  color: black;
  font-size: 1.5em;
  text-align: center;
  margin: 0 2em;
}

.about-me {
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  margin-bottom: 4em;
}

.about-me p {
  font-size: 2em;
  text-align: start;
}

.about-me img {
  border-radius: 10px;
  margin: 0 3em;
  display: inline;
  width: 100%;
  max-width: 400px;
  height: auto;
  float: left;
}

/* MY EXPERIENCE */

.experience {
  background-color: rgb(43, 43, 209);
  display: grid;
  grid-template-columns: auto;
}

.description{
  text-align: start;
}

.experience h2 {
  color: white;
  font-size: 2em;
  text-align: center;
  /* margin: 2em 0 1em 2em; */
}

.container {
  margin: 3em;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 0.6em;
}

.box {
  display: grid;
  border: 2px solid #000;
  border-radius: 10px;
  background-color: rgb(220, 196, 99);
  color: black;
  font-size: 1.2em;
  text-align: center;
  padding: .6em;
  margin-bottom: .6em;
}

.box h3 {
  margin-bottom: .2em;
}

.date {
  margin-top: 0;
}

/* PROJECTS */

.projects {
  margin: 3em 0;
  background-color: rgb(220, 196, 99);
  color: black;
  display: grid;
  grid-template-columns: auto;
}

.projects h2 {
  font-size: 2em;
  text-align: center;
  margin-bottom: 0;
}

.projects p {
  text-align: start;
  margin: 1em;
}

.projects img {
  padding-top:1em;
  margin: auto;
}

.projects-box {
  display: grid;
  border: 2px solid #000;
  border-radius: 10px;
  background-color: rgb(43, 43, 209);
  color: white;
  font-size: 1.2em;
  text-align: center;
  padding: .6em;
  margin-bottom: .6em;
}

.checkit,
.code {
  display: flex;
  margin: auto;
  color: black;
  width: fit-content;
  background-color: rgb(220, 196, 99);
  border-radius: 10px;
  padding: .2em .4em;
  margin:.2em auto;
}

.code {
  background-color: white;
}

.checkit:hover,
.checkit:focus,
.code:hover,
.code:focus {
  background-color: rgb(43, 43, 209);
  color: white;
}

/* TECH STACK */

.techStack {
  padding: 2em 0;
  background-color: rgb(43, 43, 209);
  text-align: center;
}

.tech-container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding:2em;

}

.tech img {
  width: 150px;
}

/* FOOTER */

.footer-container{
  background-color: rgb(220, 196, 99);
  color: black;
  padding-top: 2em;
  display: flex;
  text-align: start;
  justify-content: space-evenly;
}

.footer p {
  text-align: center;
  background-color: rgb(220, 196, 99);
  border-top: 1.5px solid black;
  padding: 1.5em ;
  margin: 0;
  color: black;
}

.footer-links ul  {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: start;
}

.footer-links li {
  margin: .5em 0;
}

.footer-links a,
.copyright a{
  color: black;
}

.footer a:focus,
.footer a:hover {
  font-size: 1.2em;
  color: rgb(43, 43, 209);
}

/* MEDIA QUERY */


/* Extra small devices (phones) */
@media only screen and (max-width: 731px) {

  .my-name {
    font-size: 2em;
    background-color: rgb(220, 196, 99);
    color: black;
    width: 1000px;
    text-align: center;
    margin-top: 0;
    padding: 20px;
    margin-bottom: 0;
  }

  nav, nav ul{
    display: grid;
    gap:.5rem;
    justify-content: start;
    padding-bottom: 10px;
    justify-content: center;
  }

  main {
    display: block;
    width: auto;
    justify-content: center;
    padding-top: 10px;
  }
  
  .name, .name h1 {
    margin: auto;
    text-align: center;
    width: 100%;
    padding-bottom: 30px;
    
  }

  .name h2 {
    margin-top: 0;
  }

  .name-img {
    display: none;
  }

  .about-me {
    display: block;
  }

  .about-me img {
    max-width: 80%;
    align-items: center;
    justify-content: center;
  }

  .about-me p {
    font-size: 1.1em;
    width: 80%;
    padding: 10px 0 0 10px;
    margin: auto;
  }

  .container {
    display: block;
  }

  .tech-container {
    display: block;
    align-items: center;
    justify-content: space-evenly;
    padding:2em;
  }

  .footer {
    width: 95%;
    margin: auto;
  }
  .footer h2 {
    font-size: 1.3em;
  }

  .footer-links {
    margin-top: 0;
    font-size: .8em;
    text-align: center;
  }
  .footer-links ul {
    display: block;
    text-align: center;
  }
}



/* Small devices (phones) */
@media only screen and (min-width: 732px) and (max-width: 959px) {
  nav, nav ul{
    display: flex;
    gap:.5rem;
    justify-content: center;
    font-size: 1em;
  }

  .name h1 {
    font-size: 2em;
    font-weight: bold;
    text-align: start;
    margin-top: .5em;
  }
  .name h3 {
    font-size: 1.5em;
  }
  
  .name h2 {
    font-size: 2em;
    font-weight: bold;
    margin-bottom: 1.4em;
  }
  
  main {
    display: flex;
    width: 100%;
    background-color: rgb(43, 43, 209);
    color: white;
    padding-bottom: 30px;
  }
  
  .name {
    margin: 0;
    padding: 0 4em ;
    width: 70%;
  }
  
  .name-img img {
    margin: 4em;
    width: 100%;
    max-width: 500px;
    height: auto;
    
  }
  
  .download:hover, 
  .download:focus {
    color: rgb(0, 0, 0);
    font-weight: bold;
    padding: 1em;
  }

  .about-me {
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    margin-bottom: 4em;
  }
  
  .about-me p {
    font-size: 1.1em;
    text-align: start;
  }
  
  .about-me img {
    border-radius: 10px;
    margin: 0 3em;
    display: inline;
    width: 100%;
    max-width: 400px;
    height: auto;
    float: left;
  }

  .container {
    display: block;
  }

  .footer {
    width: 95%;
    margin: auto;
  }
  .footer h2 {
    font-size: 1.3em;
  }

  .footer-links {
    margin-top: 0;
    font-size: .8em;
  }
  .footer-links ul {
    display: block;
    text-align: center;
  }

}


@media only screen and (min-width: 960px) and (max-width: 1106px) {

  nav, nav ul{
    font-size: 1em;
  }

  .name h2 {
    font-size: 1.6em;
  }

  .about-me p{
    font-size: 1.5em;
  }
}

